
.add-song {
  position: fixed;
  top: 0;
  bottom: 0;
  width: 100%;
  z-index: 200;
  background: #222;
}
.add-song.slide-enter-active,
.add-song.slide-leave-active {
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.add-song.slide-enter,
.add-song.slide-leave-to {
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
}
.add-song .header {
  position: relative;
  height: 44px;
  text-align: center;
}
.add-song .header .title {
  line-height: 44px;
  font-size: 18px;
  color: #fff;
}
.add-song .header .close-btn {
  position: absolute;
  top: 0;
  right: 8px;
}
.add-song .header .close-btn .icon-close {
  display: block;
  padding: 12px;
  font-size: 20px;
  color: #ffcd32;
}
.add-song .search-box-wrapper {
  margin: 20px;
}
.add-song .shortcut .list-wrapper {
  position: absolute;
  top: 165px;
  bottom: 0;
  width: 100%;
}
.add-song .shortcut .list-wrapper .list-scroll {
  height: 100%;
  overflow: hidden;
}
.add-song .shortcut .list-wrapper .list-scroll .list-inner {
  padding: 20px 20px;
}
.add-song .search-result {
  position: fixed;
  top: 124px;
  bottom: 0;
  width: 100%;
}
.add-song .top-title {
  text-align: center;
  padding: 18px 0;
  font-size: 0;
}
.add-song .top-title .icon-ok {
  margin-right: 4px;
  font-size: 14px;
  color: #ffcd32;
}
.add-song .top-title .text {
  font-size: 14px;
  color: #fff;
}


